
.container-fluid {
    position: relative;
    height: 100vh;
    width: 100%;
}

.container-fluid::before {
  content: "";
  background-image: url(../src/photos/background-port.jpg);
  background-size: cover;
  position: absolute;
  height: 1400px;
  width: 100%;
  left: 0px;
  margin-left: 0px;
  opacity: .7;
}

@media only screen and (max-width: 1480px) {
  .container-fluid::before {
    height: 200vh;
  }
}

@media only screen and (max-width: 1115px) {
  .container-fluid::before {
    height: 250vh;
  }
}

@media only screen and (max-width: 750px) {
  .container-fluid::before {
    height: 450vh;
  }
}